import { ObjectElement, ArrayElement, StringElement, BooleanElement, NullElement, NumberElement, LinkElement, RefElement } from 'minim';

ObjectElement.refract = function (val) {
  return new ObjectElement(val);
};

ArrayElement.refract = function (val) {
  return new ArrayElement(val);
};

StringElement.refract = function (val) {
  return new StringElement(val);
};

BooleanElement.refract = function (val) {
  return new BooleanElement(val);
};

NullElement.refract = function (val) {
  return new NullElement(val);
};

NumberElement.refract = function (val) {
  return new NumberElement(val);
};

LinkElement.refract = function (val) {
  return new LinkElement(val);
};

RefElement.refract = function (val) {
  return new RefElement(val);
};